.container{
  height: 300px;
  justify-content: center;
    
}
.container h1{
  text-align: center;
  background-color: rgb(17, 16, 16);
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.container .card1{
  background-color: violet;
  text-align: center;
  float: left;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 25%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: justify;
  padding: 10px;
  margin-right: 20px;
}
.container .card1 h2{
  text-align: center;
  font-weight: bolder;
}
.container .card1 p{
  text-align: center;
  font-weight: 900px;
  text-align: justify;
}
.container .card1 a{
  text-decoration: none;
  background-color: white;
  padding: 2px;
  
}
