.NavBar{
  background:rgb(46, 46, 248);
  height:80px;
  width:99%;
  position:fixed;
  font-size: 25px;
  z-index: 999;
  opacity: 0.9;
  display: flex;
  justify-content: center;
  
}

.NavBar nav ul{
  list-style: none;
  margin:0;
  padding:0;

}
.NavBar nav ul li{
display: inline-block;
margin:20px;

}
.NavBar nav ul li a{
  text-decoration: none;
  color:white;
}
.NavBar nav ul button a{
  text-decoration: none;
  font-size: 20px;
  background-color: white;
}
.NavBar a:hover{
  background-color: white;
  color: rgb(46, 46, 248);
  transition:0.3s ;
  
}

@media screen and (max-width:767px){
  body{
    height: 100px;
    
  }
}
